export default class Heart {
  constructor(game) {
    this.image = document.getElementById("imgHeart");
    this.game = game;
    this.gameWidth = game.gameWidth;
    this.gameHeight = game.gameHeight;
    this.size = 15;
    this.initial = {
      x: this.gameWidth - 30,
      y: 30
    };
  }

  update(deltaTime) {
    return true;
  }

  draw(ctx) {
    const lives = this.game.lives;
    for (var i = 0; i < lives; i++) {
      let x = this.initial.x - (i * this.size + 4);
      ctx.drawImage(this.image, x, this.initial.y, this.size, this.size);
    }

    const level = "Level: " + Number(this.game.level) + 1;
    ctx.font = "14px Roboto";
    ctx.textAlign = "center";
    ctx.fillStyle = "#fff";
    ctx.fillText(level, 30, 30);
  }
}
