export default class InputHandler {
  constructor(paddle, game) {
    this.game = game;
    this.paddle = paddle;
    document.addEventListener("keydown", (event) => {
      switch (event.key) {
        case "ArrowLeft":
          paddle.start();
          paddle.moveLeft();
          break;

        case "ArrowRight":
          paddle.start();
          paddle.moveRight();
          break;

        case "Escape":
          game.togglePause();
          break;

        case " ":
          game.start();
          break;

        default:
          break;
      }

      if (event.ctrlKey && event.altKey && event.key === "r") {
        game.restart();
      }
    });

    document.addEventListener("keyup", (event) => {
      switch (event.key) {
        case "ArrowLeft":
          paddle.stop();
          break;

        case "ArrowRight":
          paddle.stop();
          break;

        default:
          break;
      }
    });

    this.increment = document.getElementById("increment");
    this.decrement = document.getElementById("decrement");

    this.increment.addEventListener("click", () => {
      if (this.game.ball.speed.x - 1 !== 0) {
        if (this.game.ball.speed.x > 0) {
          this.game.ball.speed.x += 1;
          // this.game.ball.initialSpeed.x += 1;
        } else {
          this.game.ball.speed.x -= 1;
          // this.game.ball.initialSpeed.x -= 1;
        }
        if (this.game.ball.speed.y > 0) {
          this.game.ball.speed.y += 1;
          // this.game.ball.initialSpeed.y += 1;
        } else {
          this.game.ball.speed.y -= 1;
          // this.game.ball.initialSpeed.y -= 1;
        }
        this.paddle.maxSpeed += 1;
      }
      console.log(this.game.ball.speed.x);
      console.log(this.game.ball.speed.y);
    });

    this.decrement.addEventListener("click", () => {
      if (this.game.ball.speed.x - 1 > 1) {
        if (this.game.ball.speed.x > 0) {
          this.game.ball.speed.x -= 1;
          // this.game.ball.initialSpeed.x -= 1;
        } else {
          this.game.ball.speed.x += 1;
          // this.game.ball.initialSpeed.x += 1;
        }
        if (this.game.ball.speed.y > 0) {
          this.game.ball.speed.y -= 1;
          // this.game.ball.initialSpeed.y -= 1;
        } else {
          this.game.ball.speed.y += 1;
          // this.game.ball.initialSpeed.y += 1;
        }
        this.paddle.maxSpeed -= 1;
      }
      console.log(this.game.ball.speed.x);
      console.log(this.game.ball.speed.y);
    });
  }
}
